@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap");
html {
  font-size: 16px;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

.flex {
  display: flex;
}
.flex-jc-ctr {
  justify-content: center;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-sa {
  justify-content: space-around;
}
.flex-ai-ctr {
  align-items: center;
}
.flex-fd-col {
  flex-direction: column;
}
.flex-as-end {
  align-self: flex-end;
}

.grid {
  display: grid;
}
.grid-jc-ctr {
  justify-content: center;
}
.grid-pi-ctr {
  place-items: center;
}

@media screen and (max-width: 767px) {
  .m-flex-fd-col {
    flex-direction: column;
  }
  .m-pd-0 {
    padding: 0;
  }
  .m-body-mar-t {
    margin-top: -4em;
  }
  body {
    overflow: hidden !important;
  }
  body::before {
    left: -20.2em !important;
    top: -12em !important;
  }
  body::after {
    right: -20em !important;
    bottom: -11em !important;
  }
  main {
    height: 936px !important;
    width: 100vw !important;
    gap: 0 !important;
  }
  .text {
    text-align: center;
    padding: 0 4.5em !important;
  }
  .attribution {
    font-size: 0.7rem !important;
  }
}
body {
  font-family: "Rubik", sans-serif;
  width: 100vw;
  height: 100vh;
  background-color: white;
  position: relative;
}
body::before {
  content: "";
  position: absolute;
  width: 32em;
  height: 43.75em;
  z-index: -1;
  top: 0;
  left: -5.8em;
  background-image: linear-gradient(20deg, hsl(264deg, 100%, 61%), hsl(293deg, 100%, 63%));
  border-bottom-right-radius: 9999em;
  border-bottom-left-radius: 9999em;
}
body::after {
  content: "";
  position: absolute;
  width: 32em;
  height: 42.5em;
  z-index: -1;
  bottom: 0;
  right: -11.6em;
  background-image: linear-gradient(45deg, hsl(264deg, 100%, 61%), hsl(293deg, 100%, 63%));
  border-top-left-radius: 9999em;
  border-top-right-radius: 9999em;
  opacity: 0.025;
}
body main {
  height: 800px;
  gap: 7.875em;
  padding-left: 6.8em;
}
body main .phone-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4em 0;
}
body main .phone {
  display: flex;
  flex-direction: column;
  width: 15.5em;
  height: 31.5em;
  background-color: white;
  border: 10px solid white;
  border-radius: 28px;
  box-shadow: var(--shadow-phone);
  overflow: hidden;
  box-shadow: 3px 10px 35px rgba(0, 0, 0, 0.125);
}
body main .phone .phone-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 4.6em;
  padding: 1.2em 15px 0 15px;
  background-image: linear-gradient(to right, hsl(264deg, 100%, 61%), hsl(293deg, 100%, 63%));
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  box-shadow: var(--shadow-msg);
  position: relative;
}
body main .phone .phone-header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 22%;
  width: 56%;
  height: 1.2em;
  background-color: white;
  border-bottom-right-radius: 14px;
  border-bottom-left-radius: 14px;
}
body main .phone .phone-header .bk-btn {
  width: 100%;
}
body main .phone .phone-header .bk-btn .left {
  gap: 0.5em;
}
body main .phone .phone-header .bk-btn .left img {
  width: 24px;
  height: 24px;
  border-radius: 24px;
  border: 1px solid white;
}
body main .phone .phone-header .bk-btn .left .name {
  gap: 0.1em;
}
body main .phone .phone-header .bk-btn .left .name p {
  font-size: 0.5rem;
  color: white;
}
body main .phone .phone-header .bk-btn .left .name p:nth-of-type(1) {
  font-weight: 500;
  font-size: 0.67rem;
  opacity: 1;
}
body main .phone .phone-header .bk-btn .left .name p:nth-of-type(2) {
  opacity: 0.5;
}
body main .phone .phone-screen {
  width: 100%;
  height: 100%;
  background-color: hsl(270deg, 20%, 96%);
  font-size: 0.5rem;
  padding: 1.5em 1em;
}
body main .phone .phone-screen p {
  width: 130px;
  background-color: #EDE4F5;
  padding: 1em 10px 1em 1em;
  line-height: 1.2;
}
body main .phone .phone-screen .text-left {
  gap: 1em;
}
body main .phone .phone-screen .text-left p {
  border-radius: 8px 8px 8px 4px;
  color: hsl(276deg, 55%, 52%);
}
body main .phone .phone-screen .text-left .price {
  width: 160px;
  background-image: linear-gradient(to left, hsl(264deg, 100%, 61%), hsl(293deg, 100%, 63%));
  margin: 1em 0;
  padding: 1em 2em 1em 1em;
  border-radius: 8px 8px 8px 4px;
}
body main .phone .phone-screen .text-left .price .p-left {
  gap: 1em;
}
body main .phone .phone-screen .text-left .price .p-left .circle {
  width: 12px;
  height: 12px;
  border-radius: 12px;
  border: 1px solid white;
  opacity: 0.75;
}
body main .phone .phone-screen .text-left .price .p-left h4 {
  color: white;
  font-weight: 400;
  opacity: 0.75;
}
body main .phone .phone-screen .text-left .price h3 {
  font-size: 1.5em;
  color: white;
}
body main .phone .phone-screen .text-right {
  gap: 1em;
}
body main .phone .phone-screen .text-right p {
  background-color: white;
  color: hsl(271deg, 15%, 43%);
  border-radius: 8px 8px 4px 8px;
}
body main .phone .phone-screen .text-right p:nth-of-type(2) {
  width: max-content;
}
body main .phone .phone-screen .text-right .images {
  gap: 1em;
}
body main .phone .phone-screen .text-right .images img {
  height: 5em;
  width: auto;
  border-radius: 1em;
}
body main .phone .phone-screen .phone-type {
  width: 100%;
  position: relative;
}
body main .phone .phone-screen .phone-type input {
  height: 34px;
  width: 100%;
  background-color: white;
  border: none;
  border-radius: 34px;
  padding-left: 1.9em;
  font-size: 1.1em;
  color: rgba(0, 0, 0, 0.5);
}
body main .phone .phone-screen .phone-type input:focus {
  border: 1px solid hsl(293deg, 100%, 63%);
  outline: 1px solid hsl(293deg, 100%, 63%);
}
body main .phone .phone-screen .phone-type .send-icon {
  position: absolute;
  width: 26px;
  height: 26px;
  background-color: hsl(271deg, 36%, 24%);
  border-radius: 26px;
  right: 4px;
  bottom: 4px;
}
body main .text {
  width: 28.125em;
  gap: 1.4em;
}
body main .text h1 {
  font-size: 2.5rem;
  font-weight: 500;
  color: hsl(271deg, 36%, 24%);
}
body main .text p {
  color: hsl(270deg, 7%, 64%);
  line-height: 1.8;
}

.attribution {
  position: absolute;
  width: 100vw;
  height: 40px;
  bottom: 0;
  font-size: 0.8rem;
  gap: 0.5rem;
  opacity: 0.5;
}
.attribution p {
  color: black;
}
.attribution a {
  color: blue;
  text-decoration: none;
}